.index_bkgd {
	background-color: #4c66a4;
}

.main_text {
	text-align: center;
	width: 50%;
	margin-top: 100px 
}

.index_logo {
	position: fixed;
	top: 15px;
	left: 20px;
	font-family: 'Changa One', cursive;
	font-size: 95px;
	color: white;
	line-height: 70%;
}

#italics {
	font-style: italic;
}

.description {
	font-size: 40px;
	margin-bottom: 20px;
	color: white;
	width: 50%;
	text-align: center;
	margin: 0 auto;
	padding-top: 300px;
	font-family: 'Raleway', sans-serif;
}

.motivation {
	margin-top: 20px;
}

#sign_in {
	position: fixed;
	right: 5px;
	top: 5px;
	color: white;
	font-size: 15px;
}

#index_links {
	color: white;
}

#bold {
	transition: border-radius 1s ease, color .5s ease;
	font-weight: 700;
	background-color: #B7B7B7;
	margin-left: 70px;
	margin-right: 70px;
	border-radius: 5px;
}

#bold:hover {
	border-radius: 50px;
	color: black;
}

.stop-smoking-signup-link:hover {
	color: black;
	cursor: pointer;
}

.btn:hover {
	color: black;
}

